import { Component, OnInit } from '@angular/core';
import { ComonService } from '../../comon.service.component';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'school-studentdetails',
  templateUrl: './studentdetails.component.html',
  styleUrls: ['../../app.component.scss']
})
export class StudentdetailsComponent implements OnInit {
  selectedclass:any;
  allclasses: any;
  submitted = false;
  showdetails = false;
  errorMessage: any;
  editRowId: string;
  studentdetails:any;
  schoolcode: any;
  constructor(private ComonService:ComonService,private DatePipe :DatePipe ){

    this.schoolcode = ComonService.getschoolcode();
  }
  ngOnInit() {
    this.getclassnames();
  }

  getclassnames(){
    let serviceurl = '/Wcf_VS2015.SVC/GetClassNamesbycode';
    var inputparams =
    {
      SUCCODESenapathi: [{ SCHOOLCode: this.schoolcode }]
    }
      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       this.allclasses = JSON.parse(JSON.parse(Response.GetClassNamesbycodeResult)[0].VALUE);
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }

  getdetails() {
    if(this.selectedclass){
      this.submitted = true;
    this.showdetails =true; 
      let serviceurl = '/Wcf_VS2015.SVC/GetStudentDetailsbySC';
    var inputparams =
    {SUCCODESenapathi:[{OrganisationCode:this.schoolcode,ClassId:this.selectedclass}]}

      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
      this.studentdetails = JSON.parse(Response.GetStudentDetailsbySCResult);
      this.studentdetails.map((item)=>{
        let temp = item.DOB.split('(')[1].split(")")[0];
        item.DOB = this.DatePipe.transform(temp,"dd/MM/yyyy");
      })
      console.log(Response)
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
    }
     
  }
  savedata(data){
    
      let serviceurl = '/Wcf_VS2015.SVC/UpdateStudentDetails';
      data.OrganisationCode = this.schoolcode;
      data.ClassId = this.selectedclass;
      data.Name = data.ParentName;
      delete data.ParentName;
      if(data.DOB instanceof Date){
        data.DOB = this.DatePipe.transform(data.DOB,"MM/dd/yyyy");
      }
    var inputparams =
    {"SUCCODESenapathi":[
      data
    ]}
    

      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
      // this.studentdetails = JSON.parse(Response.GetStudentDetailsbySCResult);
      console.log(Response)
      this.getdetails()
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
    this.editRowId = '';
  }
   edit(val){   
     this.editRowId = val;
   }
   removeItem(data) {
    let serviceurl = '/Wcf_VS2015.SVC/DeleteStudent';
    let details ={OrganisationCode:'',ClassId:'',StudentCode:''};
    details.OrganisationCode = this.schoolcode;
    details.ClassId = this.selectedclass;
    details.StudentCode = data.StudentCode
  var inputparams =
  {
    SUCCODESenapathi:[
    details
  ]
} 
  this.ComonService.getData(serviceurl,inputparams).subscribe(
    Response =>{
    console.log(Response);
    this.getdetails()
    },
    error => {
      this.errorMessage = <any>error;
      // alert(this.errorMessage);
    })
 }

}



