import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ComonService } from '../../comon.service.component';
import {DatePipe} from '@angular/common';

@Component({
  selector: 'school-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  colorScheme = {
    domain: ['#008000', '#ff0000']
  };
  view: any[] = [500,300];
  errorMessage: any;
  schoolcode: any;
  schoolfeecollection: any;
  PresentCount: number=0;
  absentstudent: any;
  membercount: number = 0;
  showactiveuserpiechart: boolean;
  birthdaydetails: any;
  staffdetails: any;
  staffcount: any=0;
  stafftotalcount: any=0;
  constructor(private router:Router,private ComonService:ComonService,private datepipe:DatePipe) { 
    this.schoolcode = ComonService.getschoolcode();
  }
  multi:any;


  ngOnInit() {
    this.getfeecollectiondata();
    this.getclasscountbyocd();
    this.getabsentdetails();
    this.getBirthdaydetails();
    this.getstaffdetails();
  }

  getabsentdetails(){
    let date = new Date();
    let inputparams = {
      DayAttendence:
      [{OCODE:this.schoolcode,CLASSID:"",AttDay:date.getDate(),AttMonth:date.getMonth()+1,MODE:3,PeriodNo:1}]
    };
    let serviceurl = "/Wcf_VS2015.SVC/GetDayAttendence";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       let result = Response.GetDayAttendenceResult ;
        if(result){
          result = JSON.parse(Response.GetDayAttendenceResult);
          this.absentstudent = result.length;
        } else {
          this.absentstudent = 0;
        }
      },
      error => {
        this.errorMessage = <any>error;
      })
  }

  getstaffdetails(){
    let inputparams =
    {
      GetStaffCount:[{SchoolCode:this.schoolcode}]
    }
    let serviceurl = "Wcf_VS2015.SVC/GetStaffCount";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       if(JSON.parse(Response.GetStaffCountResult)[0].VALUE){
         this.staffdetails = JSON.parse(JSON.parse(Response.GetStaffCountResult)[0].VALUE)[0];
         if(this.staffdetails){
           this.staffcount = this.staffdetails.Count;
           this.stafftotalcount = this.staffdetails.TotalCount;
         }
       }
      },
      error => {
        this.errorMessage = <any>error;
      })
  }
  getBirthdaydetails(){
    let date = new Date();
    let startdate = this.datepipe.transform(date,"MM/dd/yyyy");
    
    let todate = new Date(date.setDate( date.getDate() + 1 ));
    let enddate =this.datepipe.transform(todate,"MM/dd/yyyy")

    let inputparams =
    {
      GetStudentBirthday: [{
        SchoolCode: this.schoolcode, StartDate: startdate, EndDate: enddate
      }]
    };
    let serviceurl = "Wcf_VS2015.SVC/GetStudentBirthdayList";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        if(JSON.parse(Response.GetStudentBirthdayListResult)[0].VALUE){
          this.birthdaydetails = JSON.parse(JSON.parse(Response.GetStudentBirthdayListResult)[0].VALUE)
        }
       
        
      },
      error => {
        this.errorMessage = <any>error;
      })
  }
  getclasscountbyocd(){
    let inputparams = {
      GetClassCount:[
        {OrganizationCode:this.schoolcode}]
    } ;
    let serviceurl = "Wcf_VS2015.SVC/GetClassCountbyOCODE";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        let response = JSON.parse(Response.GetClassCountbyOCODEResult);

        if(response){
          response.map((item)=>{
            this.PresentCount += parseInt(item.PresentCount);
          })
        } else {
          this.PresentCount = 0;
        }
        
        this.getmembersaccountdetails(); 
      },
      error => {
        this.errorMessage = <any>error;
      })

  }
  getmembersaccountdetails(){
    let inputparams = {
      Membersaccount:
      [{Mode:2,OrganisationCode:this.schoolcode}]
    };
    let serviceurl = "Wcf_VS2015.SVC/Membersaccount";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        this.membercount = JSON.parse(JSON.parse(Response.MembersaccountResult)[0].VALUE)[0].MembersCount;
        this.showactiveuserpiechart = true;
        this.multi= [
          {
            "name": "App Users",
            "value":this.membercount,
          },
          {
            "name": "App Not Users",
            "value":  this.PresentCount - this.membercount
          }]
      },
      error => {
        this.errorMessage = <any>error;
      })

  }
  getfeecollectiondata(){

    let date = new Date();
    let startdate = this.datepipe.transform(date,"MM/dd/yyyy");
    
    let todate = new Date(date.setDate( date.getDate() + 1 ));
    let enddate =this.datepipe.transform(todate,"MM/dd/yyyy")

    let inputparams = {
      FEES:
      [{Mode:2,OCODE:this.schoolcode,FromDate:startdate,ToDate:enddate}]
    } ;
    let serviceurl = "Wcf_VS2015.SVC/SchoolFeesCollection";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        if(JSON.parse(Response.SchoolFeesCollectionResult)[0].VALUE){
          this.schoolfeecollection = JSON.parse(JSON.parse(Response.SchoolFeesCollectionResult)[0].VALUE)[0].SchoolFeesCollection;
        } else {
          this.schoolfeecollection = 0;
        }
        
      },
      error => {
        this.errorMessage = <any>error;
      })
  }

}



