import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ComonService } from '../../comon.service.component';

@Component({
  selector: 'school-review',
  templateUrl: './review.component.html',
  styleUrls: ['./review.component.scss']
})
export class ReviewpageComponent implements OnInit {
  submitted = false;
  gethomeworkdetails: FormGroup;
  allclasses: any;
  errorMessage: any;
  allstudents:any;
  showpopup:boolean = false;
  homewroksuccesmessaage: string;
  schoolcode: any;
  studentcode:any;
  selectedclass:any;

  constructor(private formbuilder: FormBuilder,private ComonService:ComonService) { 
    this.schoolcode = ComonService.getschoolcode()
  }

  ngOnInit() {
    this.gethomeworkdetails = this.formbuilder.group({
      class: ['', Validators.required],
      studentcode: ['', Validators.required],
      description: ['', Validators.required],
      submittedby: ['', Validators.required],
    });
    this.getclassnames();
  }

  getclassnames(){
    let serviceurl = '/Wcf_VS2015.SVC/GetClassNamesbycode';
    // let getsubjecturl = '/Wcf_VS2015.SVC/GetSubjectbyOCODE';
    let inputparams =
    {
      SUCCODESenapathi: [{ SCHOOLCode: this.schoolcode }]
    }
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       this.allclasses = JSON.parse(JSON.parse(Response.GetClassNamesbycodeResult)[0].VALUE);
      },
      error => {
        this.errorMessage = <any>error;
      })
  }

  Onclasschange(id){
    this.studentcode = '';
    this.allstudents = [];
    let serviceurl = '/Wcf_VS2015.SVC/GetStudentDetailsByOC';
    let inputparams = {StuOC:[{SCHOOLCode:this.schoolcode,ClassId:parseInt(id)}]};
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        let resultset = JSON.parse(Response.GetStudentDetailsByOCResult)[0].VALUE;
        if(resultset.includes('Students not yet registered')){
          this.allstudents = [];
        } else {
          this.allstudents = JSON.parse(resultset);
        }
     
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }
  updatehomeworkdetails(data) {
    this.submitted = true;
    if (this.gethomeworkdetails.invalid) {
        return;
    }

    let getclassdetails = this.allclasses.filter((item)=>{
      return (item.Id === parseInt(this.gethomeworkdetails.value.class))
    })

    let serviceurl = '/Wcf_VS2015.SVC/InsertTopicsM';
    let inputparams = {
      TopicsM:
      [{OCODE:this.schoolcode,TID:'1801',SCODE:this.gethomeworkdetails.value.studentcode,BCT:'Student',
      SENDER:this.gethomeworkdetails.value.submittedby,LID:this.schoolcode,DESCRIPTION:this.gethomeworkdetails.value.description,
      PDATE:new Date(),EDATE:new Date,AID:'',CLASSNAME:getclassdetails[0].ClassName,SIGNATURE:''
    }]
  }
  this.ComonService.getData(serviceurl,inputparams).subscribe(
    Response =>{
    this.gethomeworkdetails.reset();
    this.submitted = false;
    this.showpopup = true;
    this.homewroksuccesmessaage ="Submitted successfully";
    },
    error => {
      this.errorMessage = <any>error;
    })
  }
  closepopup(){
    this.showpopup = false;
  }

}



