import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ComonService } from '../../comon.service.component';

@Component({
  selector: 'school-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginform:FormGroup;
  submitted :boolean = false;
  errorMessage: any;
  errormsgshow:boolean;

  constructor(private router:Router,private formbuilder:FormBuilder,private ComonService:ComonService) { }

  ngOnInit() {
    this.loginform = this.formbuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  Login(){
    this.submitted = true;
    if (this.loginform.invalid) {
      return;
  }
  
  let inputparams = 
 {
   LoginExec:[{UCODE: this.loginform.value.username,Password: this.loginform.value.password}]
  }
  let serviceurl = '/Wcf_VS2015.SVC/Login';
  this.ComonService.getData(serviceurl,inputparams).subscribe(
        Response =>{
          this.submitted = false;
          this.loginform.reset();
          console.log(Response);
          if(Response.LoginResult === 'Fail'){
            this.errormsgshow = true;
            this.errorMessage = 'Invalid credentials';
          } else {
            
              let schoolcode = JSON.parse(JSON.parse(Response.LoginResult)[0].VALUE)[0].UCODE;
              this.ComonService.setschoolcode(schoolcode);
              this.getSchooldetails(schoolcode)
          }
         
        },
        error => {
          this.errormsgshow = true;
          this.errorMessage = <any>error;
        })
   
  }
  closepopup(){
    this.errormsgshow = false;
  }
  getSchooldetails(schoolcode){
    let inputparams = 
    {
      SUCCODESenapathi: [{ SCHOOLCode: schoolcode}]
    }
    let serviceurl = '/Wcf_VS2015.SVC/GetSchoolDetailsByscode2';
    this.ComonService.getData(serviceurl,inputparams).subscribe(
          Response =>{
            this.submitted = false;
            let response = JSON.parse(JSON.parse(Response.GetSchoolDetailsByscode2Result)[0].VALUE);
            this.ComonService.setorganizationdata(response);
            this.router.navigate(['/dashboard']);
          },
          error => {
            this.errorMessage = <any>error;
          })
  }

}



