import { NgModule }       from '@angular/core';
import {RouterModule,Routes} from '@angular/router';
import { AppComponent } from './app.component';
import { FeecollectionComponent } from './pages/feecollection/feecollection.component';
import { StudentdetailsComponent } from './pages/studentdetails/studentdetails.component';
import{UpdatemarksComponent} from './pages/updatemarks/updatemarks.component';
import { SupportpageComponent } from './pages/supportpage/supportpage.component';
import { HomeworkpageComponent } from './pages/homeworkpage/homeworkpage.component';
import { GetstudentdetailsComponent } from './pages/getstudentdetails/getstudentdetails.component';
import { LoginComponent } from './pages/login/login.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import {ReviewpageComponent} from './pages/review/review.component';
import {RemarkpageComponent} from './pages/remark/remark.component';


const ROUTES: Routes = [
   
    {
        path:'feecollection',
        component:FeecollectionComponent
    },
    {
        path:'updatestudentdetails',
        component:StudentdetailsComponent
    },
    {
        path:'updatestudentmarks',
        component: UpdatemarksComponent
    },
    {
        path:'homework',
        component: HomeworkpageComponent
    },
    {
        path:'getstudetdetails',
        component: GetstudentdetailsComponent
    },
    {
        path:'dashboard',
        component: DashboardComponent
    },
    {
        path:'reviews',
        component: ReviewpageComponent
    },
    {
        path:'remarks',
        component: RemarkpageComponent
    },
    {
        path:'login',
        component: LoginComponent
    },
    {
        path:'supportpage',
        component: SupportpageComponent
    },
    {
        path: '**',
        redirectTo: '/feecollection',
        pathMatch: 'full'
    }
];
@NgModule({
    imports: [ RouterModule.forRoot(ROUTES) ],
    exports: [ RouterModule ]
  })
  export class RoutesModule{

  }


