import { NgModule, Injectable } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class ComonService {
  organizationdata: any;
  schoolcode:any;

  constructor(private http: HttpClient) {

  }
  getData(url: string, param: any): Observable<any> {
    return this.http.post(url, param, httpOptions)
      .map(this.extractData)
      .catch(this.handleError);
  }
  private extractData(res: Response) {
    const body = res;
    return body || {};
  }
  getmethodData(url: string, param: any): Observable<any> {
    return this.http.get(url)
      .map(this.extractData1)
      .catch(this.handleError);
  }
  private extractData1(res: Response) {
    const body = res;
    return body || {};
  }
  private handleError(error: any) {
    const errMsg = (error.message) ? error.message :
      error.status ? `${error.status} - ${error.statusText}` : 'Server error';
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

  setorganizationdata(data){
    this.organizationdata = data;
  }

  getorganizationdata(){
    return this.organizationdata;
  }
  setschoolcode(data){
    this.schoolcode = data;
  }

  getschoolcode(){
    return this.schoolcode;
  }


}


