import { Component, OnInit } from '@angular/core';
import { FormGroup,FormBuilder , Validators ,FormArray } from '@angular/forms';
import { ComonService } from '../../comon.service.component';

@Component({
  selector: 'school-updatemarks',
  templateUrl: './updatemarks.component.html',
  styleUrls: ['../../app.component.scss']
})
export class UpdatemarksComponent implements OnInit {
  getexamdetails: FormGroup;
  submitted = false;
  showdetails = false;
  selectedclass:any;
  errorMessage:string;
  allclasses: any;
  examdetails:any;
  updatemarks =  [{name:"chandrakanth",Maths:90,physics:89,English:78,Telugu:90,Chemistry:90,Hindi:80},
    {name:"chandrakanth",Maths:90,physics:89,English:78,Telugu:90,Chemistry:90,Hindi:80},
    {name:"chandrakanth",Maths:90,physics:89,English:78,Telugu:90,Chemistry:90,Hindi:80}]
  schoolcode: any;
  constructor(private formbuilder: FormBuilder,private ComonService:ComonService) { 
    this.schoolcode = ComonService.getschoolcode();
  }

  ngOnInit() {
    this.getexamdetails = this.formbuilder.group({
      class: ['', Validators.required],
      examdetails: ['', Validators.required]
    });
    this.getclassnames();
    this.getExamdetails();
  }

  getclassnames(){
    let serviceurl = '/Wcf_VS2015.SVC/GetClassNamesbycode';
    var inputparams =
    {
      SUCCODESenapathi: [{ SCHOOLCode: this.schoolcode}]
    }
      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       this.allclasses = JSON.parse(JSON.parse(Response.GetClassNamesbycodeResult)[0].VALUE);
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }
  getExamdetails(){
    let serviceurl = 'Wcf_VS2015.SVC/GetExamTypebyOCODE';
    var inputparams =
    {
      TOPICM:[{OrganisationId:this.schoolcode}]
    }
      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       this.examdetails = JSON.parse(Response.GetExamTypebyOCODEResult);
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }
  updatestudentsmarks(data) {
    this.submitted = true;
    // this.showdetails =true; 
    if (this.getexamdetails.invalid) {
        return;
    }
    let serviceurl = 'Wcf_VS2015.SVC/StudentMarks';
    var inputparams =
    {
      StudentMarks:[{Mode:'Marks',ClassId:data.value.class,ExamTypeId:data.value.examdetails}]
    }
      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        let result = JSON.parse(Response.StudentMarksResult)[0].VALUE;
       if(result){
        JSON.parse(JSON.parse(Response.StudentMarksResult)[0].VALUE)
       }
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
     
  }
}



