import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import { HeaderComponent } from './pages/header/header.component';
import { FooterComponent } from './pages/footer/footer.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { HttpClientModule } from '@angular/common/http';
import { FeecollectionComponent } from './pages/feecollection/feecollection.component';
import { RoutesModule } from './Route.modules';
import { RouterModule } from '@angular/router';
import { ComonService } from './comon.service.component';
import { FormsModule,ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { StudentdetailsComponent } from './pages/studentdetails/studentdetails.component';
import { UpdatemarksComponent } from './pages/updatemarks/updatemarks.component';
import { DatePipe } from '@angular/common';
import { SupportpageComponent } from './pages/supportpage/supportpage.component';
import { HomeworkpageComponent } from './pages/homeworkpage/homeworkpage.component';
import { ConfirmpopupComponent } from './popups/confirmpopup/confirmpopup.component';
import { GetstudentdetailsComponent } from './pages/getstudentdetails/getstudentdetails.component';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LoginComponent } from './pages/login/login.component';
import { RemarkpageComponent } from './pages/remark/remark.component';
import { ReviewpageComponent } from './pages/review/review.component';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    DashboardComponent,
    FeecollectionComponent,
    StudentdetailsComponent,
    UpdatemarksComponent,
    SupportpageComponent,
    HomeworkpageComponent,
    ConfirmpopupComponent,
    GetstudentdetailsComponent,
    LoginComponent,
    RemarkpageComponent,
    ReviewpageComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RouterModule,
    RoutesModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    NgxChartsModule,
    BrowserAnimationsModule
  ],
  providers: [ComonService,DatePipe],
  bootstrap: [AppComponent]
})
export class AppModule { }



