import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ComonService } from '../../comon.service.component';

@Component({
  selector: 'school-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  
  Organizationname:any;
  loginperson: any;
  organizationimage: any;

  constructor(private router :Router,private ComonService:ComonService) { 
    let schooldetails = ComonService.getorganizationdata();
    if(schooldetails){
      this.Organizationname = schooldetails[0].OrganizationName;
      this.loginperson = schooldetails[0].AuthorisedPersonName;
      this.organizationimage = schooldetails[0].OrganizationPhoto;
    }
  }

  ngOnInit() {
  }

 
  logout(){
    this.router.navigate(['/login']);
  }

}



