import { Component, OnInit } from '@angular/core';
import { FormGroup,FormBuilder , Validators ,FormArray } from '@angular/forms';
import { ComonService } from '../../comon.service.component';

@Component({
  selector: 'school-feecollection',
  templateUrl: './feecollection.component.html',
  styleUrls: ['../../app.component.scss']
})
export class FeecollectionComponent implements OnInit {

  feecollectionform: FormGroup;
  feecollectionpay: FormGroup;
  submitted = false;
  paysubmitted = false;
  showdetails = false;
  errorMessage:string;
  allclasses: any;
  selectedclass:any;
  studentcode:any;
  allstudents: any;
  feetype:any;
  paidamount: any = 0;
  totalfee: number = 0;
  concessionamount: any = 0;
  allfeetypes: any;
  moduleid:any;
  student_code: any;
  classid: any;
  printpagedetails:any;
  printpage:boolean =false;
  receiptnumber: any;
  a = ['','One ','Two ','Three ','Four ', 'Five ','Six ','Seven ','Eight ','Nine ','Ten ','Eleven ','Twelve ','Thirteen ','Fourteen ','Fifteen ','Sixteen ','Seventeen ','Eighteen ','Nineteen '];
  b = ['', '', 'Twenty','Thirty','Forty','Fifty', 'Sixty','Seventy','Eighty','Ninety'];
  currencyinwords: string;
  Organizationname: any;
  organizationimage: any;
  organizationaddress: any;
  schoolcode: any;
  constructor(private formbuilder: FormBuilder,private ComonService:ComonService){
    this.schoolcode = ComonService.getschoolcode();

  }
 
  ngOnInit() {
    this.feecollectionform = this.formbuilder.group({
      class: ['', Validators.required],
      studentcode: ['', Validators.required]
    });
    this.feecollectionpay = this.formbuilder.group({
      feetype: ['', Validators.required],
      amount: ['', Validators.required],
      collectedfrom: ['', Validators.required],
      receivedby: ['', Validators.required],
      contactno: ['', Validators.required],
    });
    this.getclassnames();
    this.changedcurrencytowords(2100);
  }


  getclassnames(){
    let serviceurl = '/Wcf_VS2015.SVC/GetClassNamesbycode';
    var inputparams =
    {
      SUCCODESenapathi: [{ SCHOOLCode: this.schoolcode }]
    }
      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       this.allclasses = JSON.parse(JSON.parse(Response.GetClassNamesbycodeResult)[0].VALUE);
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }

  changedcurrencytowords (num) {
    if ((num = num.toString()).length > 9) return 'overflow';
    var n = ('000000000' + num).substr(-9).match(/^(\d{2})(\d{2})(\d{2})(\d{1})(\d{2})$/);
    if (!n) return; var amountinwords = '';
    amountinwords += (Number(n[1]) !== 0) ? (this.a[Number(n[1])] || this.b[n[1][0]] + ' ' + this.a[n[1][1]]) + 'Crore ' : '';
    amountinwords += (Number(n[2]) !== 0) ? (this.a[Number(n[2])] || this.b[n[2][0]] + ' ' + this.a[n[2][1]]) + 'Lakh ' : '';
    amountinwords += (Number(n[3]) !== 0) ? (this.a[Number(n[3])] || this.b[n[3][0]] + ' ' + this.a[n[3][1]]) + 'Thousand ' : '';
    amountinwords += (Number(n[4]) !== 0) ? (this.a[Number(n[4])] || this.b[n[4][0]] + ' ' + this.a[n[4][1]]) + 'Hundred ' : '';
    amountinwords += (Number(n[5]) !== 0) ? ((amountinwords != '') ? 'and ' : '') + (this.a[Number(n[5])] || this.b[n[5][0]] + ' ' + this.a[n[5][1]]) + 'only ' : '';
    this.currencyinwords = amountinwords;
}

  Onclasschange(id){
    this.studentcode = '';
    this.showdetails = false;
    this.allstudents = [];
    let serviceurl = '/Wcf_VS2015.SVC/GetStudentDetailsByOC';
    let inputparams = {StuOC:[{SCHOOLCode:this.schoolcode,ClassId:parseInt(id)}]};
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        let resultset = JSON.parse(Response.GetStudentDetailsByOCResult)[0].VALUE;
        if(resultset.includes('Students not yet registered')){
          this.allstudents = [];
        } else {
          this.allstudents = JSON.parse(resultset);
        }
     
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }

  getstandards(data){
    let inputparams = {Standards:[{OCODE:this.schoolcode,CLASSID:data.class,SCODE:data.studentcode}]};
    let serviceurl = "/Wcf_VS2015.SVC/GetFeesStandards";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        // let resultset = JSON.parse(JSON.parse(Response._body));
        console.log(Response);
        let resultset = JSON.parse(Response.GetFeesStandardsResult);
        if(resultset[0].STATUS === 'Success'){
          let feedetails = JSON.parse(resultset[0].VALUE);
          let tot = 0;
          feedetails.map((item)=>{
            tot += item.Amount;
          })
          this.totalfee = tot;
        }
        
     
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }
  getstudentfeedetails(data){
    let inputparams =  {FeesDetails:[{FromDate:'',ToDate:'',SCODE:data.studentcode}]};
    let serviceurl = "/Wcf_VS2015.SVC/StudentFeeDetils";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        // let resultset = JSON.parse(JSON.parse(Response._body));
        console.log(Response);
        let resultset = JSON.parse(Response.StudentFeeDetilsResult); 
        if(resultset.length > 0){
          this.moduleid = resultset[0].MODULEID;
          let tot = 0;
          resultset.map((item)=>{
            tot += item.TotalAmountPaid;
          })

          this.paidamount = tot;
        } else {
          this.paidamount = 0;
        }
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }
  getstudentfeediscount(data){
    let inputparams = {Discount:[{OCODE:this.schoolcode,CLASSID:data.class,SCODE:data.studentcode}]};
    this.classid = data.class;
    this.student_code = data.studentcode;
    let serviceurl = "/Wcf_VS2015.SVC/GetFeesDiscountStandards";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        // let resultset = JSON.parse(JSON.parse(Response._body));
        console.log(Response);
        let resultset = JSON.parse(Response.GetFeesDiscountStandardsResult);
        if(resultset[0].STATUS === 'Success'){
          let feedetails = JSON.parse(resultset[0].VALUE);
          let tot = 0;
          feedetails.map((item)=>{
            tot += item.Amount;
          })
          this.concessionamount = tot;    
        }
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }

  getfeestype(){
    let inputparams =this.schoolcode;
    let serviceurl = "/Wcf_VS2015.SVC/GetStandardModule/"+inputparams;
    this.ComonService.getmethodData(serviceurl,inputparams).subscribe(
      Response =>{
        // let resultset = JSON.parse(JSON.parse(Response._body));
        console.log(Response);
        this.allfeetypes = JSON.parse(Response.GetStandardModuleResult);
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }
  onfeedetailsSubmit(data) {
    this.submitted = true;
    this.feetype = '';
    this.allfeetypes = [];
    if (this.feecollectionform.invalid) {
        return;
    }
    this.showdetails =true;
    this.getfeestype();
    this.getstandards(data.value);
    this.getstudentfeediscount(data.value);
    this.getstudentfeedetails(data.value);
    
  }
  Onpaysubmit(data) {
    this.paysubmitted = true;
    if (this.feecollectionpay.invalid) {
        return;
    } 

    let classdetails = {classname:'',classlevel:''}
    this.allclasses.map((item)=>{
      if(item.Id === parseInt(this.classid)){
        classdetails.classname = item.ClassName;
        classdetails.classlevel = item.ClassLevel;
      }
    })
    let studentname ='';
    this.allstudents.map((item)=>{
      if(item.StudentCode === this.student_code ){
        studentname = item.StudentName;
      }
    })
   let inputparams = { FEES: [{ OCODE:this.schoolcode,CLASSID:this.classid,CLASSNAME:classdetails.classname,CLASSLEVEL:classdetails.classlevel, FEESMODULENAME:this.allfeetypes[data.value.feetype].ModuleName, 
    AMOUNT:data.value.amount, CURRENCY:"Rupees", SCODE:this.student_code , STUDENTNAME:studentname, ID:this.allfeetypes[data.value.feetype].ID, CreateBy:this.schoolcode, 
    CreateOn:"07/17/2019 21:35:01", ModifiedBy:"", ModifiedOn:"", MODULEID:this.moduleid, PARENTCONTACTNUMBER:data.value.contactno, 
    COLLECTEDFROM:data.value.collectedfrom, RECEIVEDBY:data.value.receivedby, DateTime: new Date(), PARENTSIGNATURE:""}] }
    this.changedcurrencytowords(data.value.amount);
    this.printpagedetails = inputparams.FEES;
    let serviceurl = "/Wcf_VS2015.SVC/FeesPayment";
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        let resultset = JSON.parse(Response.FeesPaymentResult);
        if(resultset[0].STATUS === 'Success'){
          this.feecollectionpay.reset();
          this.paysubmitted = false;
          let schooldetails = this.ComonService.getorganizationdata();
          if(schooldetails){
            this.Organizationname = schooldetails[0].OrganizationName;
            this.organizationaddress = schooldetails[0].OrganizationAddress;
            this.organizationimage = schooldetails[0].OrganizationPhoto;
          }
        this.printpage = true;
        this.receiptnumber = JSON.parse(Response.FeesPaymentResult)[0].VALUE;
        } else {
          alert("Payment failed");
        }
        // this.allfeetypes = JSON.parse(Response.FeesPaymentResult);
      },
      error => {
        this.errorMessage = <any>error;
        alert(this.errorMessage);
      })
  }

  print(){
    window.print();
    this.printpage = false;
  }

}



