import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ComonService } from '../../comon.service.component';


@Component({
  selector: 'school-getstudentdetails',
  templateUrl: './getstudentdetails.component.html',
  styleUrls: ['./getstudentdetails.component.scss']
})

export class GetstudentdetailsComponent implements OnInit {
  single: any[];
  multi:any = [
    {
      "name": "Telugu",
      "series": [
        {
          "name": "Maximum Marks",
          "value": 100
        },
        {
          "name": "Minimum Marks",
          "value": 25
        },
        {
          "name": "Average Marks",
          "value": 55
        },
        {
          "name": "Obtained Marks",
          "value": 98
        }
      ]
    },
    {
      "name": "Maths",
      "series": [
        {
          "name": "Maximum Marks",
          "value": 100
        },
        {
          "name": "Minimum Marks",
          "value": 97
        },
        {
          "name": "Average Marks",
          "value": 12
        },
        {
          "name": "Obtained Marks",
          "value": 94
        }
      ]
    },
  
    {
      "name": "English",
      "series": [
        {
          "name": "Maximum Marks",
          "value": 87
        },
        {
          "name": "Minimum Marks",
          "value": 98
        },
        {
          "name": "Average Marks",
          "value": 15
        },
        {
          "name": "Obtained Marks",
          "value": 98
        }
      ]
    },
    {
      "name": "hindi",
      "series": [
        {
          "name": "Maximum Marks",
          "value": 87
        },
        {
          "name": "Minimum Marks",
          "value": 98
        },
        {
          "name": "Average Marks",
          "value": 15
        },
        {
          "name": "Obtained Marks",
          "value": 98
        }
      ]
    },
    {
      "name": "chemistry",
      "series": [
        {
          "name": "Maximum Marks",
          "value": 87
        },
        {
          "name": "Minimum Marks",
          "value": 98
        },
        {
          "name": "Average Marks",
          "value": 15
        },
        {
          "name": "Obtained Marks",
          "value": 98
        }
      ]
    },
    {
      "name": "physics",
      "series": [
        {
          "name": "Maximum Marks",
          "value": 87
        },
        {
          "name": "Minimum Marks",
          "value": 98
        },
        {
          "name": "Average Marks",
          "value": 15
        },
        {
          "name": "Obtained Marks",
          "value": 98
        }
      ]
    }
  ];

  view: any[] = [900,400];

  // options
  showXAxis = true;
  showYAxis = true;
  gradient = false;
  showLegend = true;
  showXAxisLabel = true;
  xAxisLabel = 'Subject';
  showYAxisLabel = true;
  yAxisLabel = 'Marks';
  printpage:boolean =false;
  allclasses: any;
  selectedclass:any;
  schoolcode: any;
  showdetails = false;
  errorMessage:any;
  studentcode:any;
  allstudents:any;
  examdetails:any;
  submitted = false;
  getstudentdetails:FormGroup;
  Organizationname: any;
  organizationimage: any;
  organizationaddress: any;
  colorScheme = {
    domain: ['#5AA454', '#A10A28', '#C7B42C', '#2388d6']
  };
  constructor(private formbuilder: FormBuilder,private ComonService:ComonService) { 
    this.schoolcode = ComonService.getschoolcode();
  }


  getclassnames(){
    let serviceurl = '/Wcf_VS2015.SVC/GetClassNamesbycode';
    var inputparams =
    {
      SUCCODESenapathi: [{ SCHOOLCode: this.schoolcode }]
    }
      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       this.allclasses = JSON.parse(JSON.parse(Response.GetClassNamesbycodeResult)[0].VALUE);
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }

  Onclasschange(id){
    this.studentcode = '';
    this.showdetails = false;
    this.allstudents = [];
    let serviceurl = '/Wcf_VS2015.SVC/GetStudentDetailsByOC';
    let inputparams = {StuOC:[{SCHOOLCode:this.schoolcode,ClassId:parseInt(id)}]};
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
        let resultset = JSON.parse(Response.GetStudentDetailsByOCResult)[0].VALUE;
        if(resultset.includes('Students not yet registered')){
          this.allstudents = [];
        } else {
          this.allstudents = JSON.parse(resultset);
        }
     
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }

  print(){
    window.print();
    this.printpage = false;
  }
  getExamdetails(){
    let serviceurl = 'Wcf_VS2015.SVC/GetExamTypebyOCODE';
    var inputparams =
    {
      TOPICM:[{OrganisationId:this.schoolcode}]
    }
      
    this.ComonService.getData(serviceurl,inputparams).subscribe(
      Response =>{
       this.examdetails = JSON.parse(Response.GetExamTypebyOCODEResult);
      },
      error => {
        this.errorMessage = <any>error;
        // alert(this.errorMessage);
      })
  }
  ngOnInit() {
    this.getstudentdetails = this.formbuilder.group({
      class: ['', Validators.required],
      studentcode: ['', Validators.required],
      examdetails: ['', Validators.required]
    });
    this.getExamdetails();
    this.getclassnames();
    let schooldetails = this.ComonService.getorganizationdata();
          if(schooldetails){
            this.Organizationname = schooldetails[0].OrganizationName;
            this.organizationaddress = schooldetails[0].OrganizationAddress;
            this.organizationimage = schooldetails[0].OrganizationPhoto;
          }
  }
  getstudentsmarks(data) {
    this.submitted = true;
    // this.showdetails =true; 
    if (this.getstudentdetails.invalid) {
        return;
    }
    this.printpage = true;
    this.showdetails =true; 
     
  }

}



